.state-selection {
  text-align: center;
  padding: 40px 20px;
}

.state-selection h1 {
  font-size: 2.5rem;
  margin-bottom: 30px;
  color: #333;
}

.search-bar {
  width: 80%;
  padding: 10px;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 20px;
}

.states-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* 3 columns */
  gap: 20px;
  max-width: 1000px;
  margin: 0 auto;
}

.state-item {
  padding: 15px;
  background-color: #f4f4f4;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.state-item h3 {
  font-size: 1.5rem;
  color: #333;
}

.state-link {
  display: block;
  margin-top: 10px;
  padding: 10px;
  background-color: #4CAF50;
  color: white;
  text-decoration: none;
  border-radius: 5px;
  font-size: 1.1rem;
  font-weight: bold;
  transition: background-color 0.3s ease;
}

.state-link:hover {
  background-color: #45a049;
}

/* Responsive styles for smaller screens */
@media (max-width: 768px) {
  .states-grid {
    grid-template-columns: 1fr; /* Single column on smaller screens */
  }
}
